<template>
  <div class="page-container">
    <base-breadcrumbs
      :parents="[
    ['Home', '/'], 
    ['Klanten', '/klanten'], 
    [currentClient.data().clientName, `/klant/${currentClient.id}`], 
    [currentProject.data().projectName, `/project/${currentProject.id}`], 
    [currentRoom.data().roomName, `/ruimte/${currentRoom.id}`]  ]"
      :currentLocation="currentMeasurementDisplayName"
    />
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>
          <base-icon
            iconRef="activity"
            position="left"
          />
          {{currentMeasurementDisplayName}}
        </h2>
        <div class="flex-divider"></div>
        <base-button
          type="secondary"
          @click="openEditModal"
        >
          <base-icon
            iconRef="edit"
            position="left"
          />Bewerk
        </base-button>
      </div>
      <div class="page-box-content values">
        <div
          class="value-group"
          v-show="currentMeasurement.data().description != ''"
        >
          <div class="value">{{currentMeasurement.data().description}}</div>
        </div>
        <div class="value-row">
          <div class="value-group">
            <div class="label">Metingtype</div>
            <div class="value">{{currentMeasurement.data().type}}</div>
          </div>
          <div class="value-group">
            <div class="label">Datum</div>
            <div class="value">{{currentMeasurementDatePretty}}</div>
          </div>
        </div>
        <div class="value-row">
          <div class="value-group">
            <div class="label">125 Hz</div>
            <div class="value">{{currentMeasurement.data().hz125}}</div>
          </div>
          <div class="value-group">
            <div class="label">250 Hz</div>
            <div class="value">{{currentMeasurement.data().hz250}}</div>
          </div>
          <div class="value-group">
            <div class="label">500 Hz</div>
            <div class="value">{{currentMeasurement.data().hz500}}</div>
          </div>
          <div class="value-group">
            <div class="label">1000 Hz</div>
            <div class="value">{{currentMeasurement.data().hz1000}}</div>
          </div>
          <div class="value-group">
            <div class="label">2000 Hz</div>
            <div class="value">{{currentMeasurement.data().hz2000}}</div>
          </div>
          <div class="value-group">
            <div class="label">4000 Hz</div>
            <div class="value">{{currentMeasurement.data().hz4000}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Measurement",
  data: function() {
    return {};
  },
  computed: {
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    },
    currentProject: function() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.currentRoom.data().belongsTo;
      });
    },
    currentRoom: function() {
      return this.$store.getters.getRoomsList.find(room => {
        return room.id == this.currentMeasurement.data().belongsTo;
      });
    },
    currentMeasurement: function() {
      return this.$store.getters.getMeasurementsList.find(measurement => {
        return measurement.id == this.$route.params.measurementId;
      });
    },
    currentMeasurementDisplayName: function() {
      let displayName = this.currentMeasurement.data().type;
      if (this.currentMeasurement.data().label.length > 0)
        displayName += `, ${this.currentMeasurement.data().label}`;
      return displayName;
    },
    currentMeasurementDate: function() {
      const currentDate = new Date(1970, 0, 1);
      currentDate.setSeconds(this.currentMeasurement.data().date.seconds);
      return currentDate;
    },
    currentMeasurementDatePretty: function() {
      const months = [
        "januari",
        "februari",
        "maart",
        "april",
        "mei",
        "juni",
        "juli",
        "augustus",
        "september",
        "iktober",
        "november",
        "december"
      ];
      const day = this.currentMeasurementDate.getDate();
      const monthIndex = this.currentMeasurementDate.getMonth();
      const year = this.currentMeasurementDate.getFullYear();
      return `${day} ${months[monthIndex]} ${year}`;
    }
  },
  methods: {
    openEditModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "measurement",
        modalType: "edit",
        shown: true
      });
    }
  },
  created: function() {
    this.$setPageTitle(`Meting - ${this.currentMeasurementDisplayName}`);
  }
};
</script>


